<template>
  <div>
    <div class="way-div-one">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>任务条例</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="way-div-two">
      <el-row>
        <search-bar
          class="way-search-bar"
          ref="searchBar"
          @onSearch="searchResult"
        ></search-bar>
      </el-row>
      <div>
        <el-table
          :data="
            wayBillTask.slice(
              (currentPage - 1) * pagesize,
              currentPage * pagesize
            )
          "
          border
          :height="735 + 'px'"
          style="width: 100%"
        >
          <el-table-column prop="id" label="ID" width="150"></el-table-column>
          <el-table-column prop="taskId" label="task-id" width="200">
          </el-table-column>
          <el-table-column prop="stage" label="task阶段" width="200">
          </el-table-column>
          <el-table-column prop="wayBillTaskId" label="运单id" width="200">
          </el-table-column>
          <el-table-column
            prop="status"
            label="运单任务状态"
            width="200"
            :formatter="forStatus"
          >
          </el-table-column>
          <el-table-column prop="param" label="运单参数" width="200">
          </el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="200">
          </el-table-column>
          <el-table-column prop="updateTime" label="更新时间" width="200">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="120">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="show(scope.row)"
                >修改</el-button
              >
              <el-button
                type="text"
                size="small"
                @click="deleteWayBillTask(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10, 15, 20]"
            :page-size="10"
            layout="total, sizes, prev, pager, next, jumper"
            :total="sizes"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div>
      <el-dialog title="修改信息" :visible.sync="dialogEditFormVisible">
        <el-form :v-model="form">
          <el-form-item label="task-id">
            <el-input v-model="form.taskId"></el-input>
          </el-form-item>
          <el-form-item label="task阶段">
            <el-input v-model="form.stage"></el-input>
          </el-form-item>
          <el-form-item label="运单id">
            <el-input v-model="form.wayBillTaskId"></el-input>
          </el-form-item>
          <el-form-item label="运单任务状态">
            <el-input v-model="form.status"></el-input>
          </el-form-item>
          <el-form-item label="运单参数">
            <el-input v-model="form.param"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="clear">取 消</el-button>
          <el-button type="primary" @click="editWayBillTask">修改</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import SearchBar from "../components/SearchBar.vue";
export default {
  name: "WayBillTask",
  components: {
    SearchBar,
  },
  data() {
    return {
      dialogEditFormVisible: false,
      pagesize: 10,
      currentPage: 1,
      wayBillTask: [],
      sizes: 0,
      isSear: 0,
      form: {
        id: "",
        taskId: "",
        stage: "",
        wayBillTaskId: "",
        status: "",
        param: "",
        createTime: "",
      },
    };
  },
  mounted: function () {
    this.loadWayBillTask();
  },
  methods: {
    loadWayBillTask() {
      this.wayBillTask = [];
      for (let i = 0; i < this.currentPage; i++) {
        this.$axios
          .get(
            "http://localhost:52000/web/waybilltask/list?page=" + i + "&size=" + this.pagesize + "",
            {}
          )
          .then((resp) => {
            this.wayBillTask = this.wayBillTask.concat(resp.data.data.content);
            this.sizes = resp.data.data.totalElements;
          });
      }
    },
    deleteWayBillTask(row) {
      this.$confirm("确认删除？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$axios
            .delete("http://localhost:52000/web/waybilltask/delete?id=" + row.id, {})
            .then((resp) => {
              this.loadWayBillTask();
              this.$emit("deleteLocations", row);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    show(row) {
      this.dialogEditFormVisible = true;
      this.form = {
        id: row.id,
        taskId: row.taskId,
        stage: row.stage,
        wayBillTaskId: row.wayBillTaskId,
        status: row.status,
        param: row.param,
        createTime: row.createTime,
      };
    },
    clear() {
      this.form = {
        id: "",
        taskId: "",
        stage: "",
        wayBillTaskId: "",
        status: "",
        param: "",
      };
      this.dialogEditFormVisible = false;
    },
    editWayBillTask() {
      var date1 = dayjs(new Date().getTime() - 24 * 60 * 60 * 1000).format(
        "YYYY-MM-DD HH:mm:ss"
      );
      this.$axios
        .post("http://localhost:52000/web/waybilltask/addorupdata", {
          id: this.form.id,
          taskId: this.form.taskId,
          stage: this.form.stage,
          wayBillTaskId: this.form.wayBillTaskId,
          status: this.form.status,
          param: this.form.param,
          createTime: this.form.createTime,
          updateTime: date1,
        })
        .then((resp) => {
          if (resp && resp.data.code === 200) {
            this.$emit("editWayBillTask");
          }
          this.loadWayBillTask();
          this.clear();
        });
    },
    searchResult() {
      if (this.$refs.searchBar.keywords != "") {
        this.$axios
          .get(
            "http://localhost:52000/web/waybilltask/find?taskId=" + this.$refs.searchBar.keywords,
            {}
          )
          .then((resp) => {
            this.wayBillTask = resp.data.data;
            this.sizes = this.wayBillTask.length;
          });
      } else {
        this.isSear = 0;
        this.loadWayBillTask();
      }
    },
    handleSizeChange(val) {
      this.pagesize = val;
      if (this.isSear === 0) {
        this.loadWayBillTask();
      }
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      if (this.isSear === 0) {
        this.loadWayBillTask();
      }
    },
    forStatus(row, index) {
      if (row.status == 10) {
        row.status = "进行中";
      }
      if (row.status == 20) {
        row.status = "已完成";
      }
      return row.status;
    },
  },
};
</script>

<style>
.way-div-one {
  margin: 20px 0px;
}
.way-div-two {
  overflow: hidden;
  margin: 5px 10px;
}
.way-search-bar {
  float: left;
}
.way-add {
  float: right;
}
</style>


